//store/index.jsstore/index.jsvuex
//store/index.js
// user-api
import { getCouponListForPage,insertCoupon 
	,updateCoupon,findCouponById,deleteCouponById,setCouponIsOnSale,insertCouponMultiple} from '../../../api/coupon-api.js'
import { getCouponTypeList } from '../../../api/coupon-type-api.js'
// 
export default{
	// mapState
	namespaced:true,
	// 
	// statevuedata
	//statemapStatevuecomputedstatevue
	state:{
		list:[],//table
		page:{//
			pno:1,
			psize:10,
			pCount:0,
			totalElements:0
		},
		couponTypeList:[]
	},
	// mutationsstate
	// mapState
	// mutationsstate
	//mutationmapMutationsvuemethods
	mutations:{
		// statelist
		setList(state,list){
			state.list = list;
		},
		// statepage
		setPage(state,page){
			state.page = page
		},
		// state
		setCouponTypeList(state,couponTypeList){
			state.couponTypeList = couponTypeList
		}
	},
	// gettersvuexcomputedstategetter
	getters:{
		getList(state,getter){
			return state.list
		}
	},
	
	actions:{
		// 
		//actions{commit,dispatch}mutationsactions
		//vuejson
		async getListForPage({commit},queryForm){
			// 
			let res = await getCouponListForPage(queryForm);
			if(res.data.code == 200){
				// code200listpage
				//mutationslistpage
				commit('setList',res.data.data.list)
				commit('setPage',res.data.data.page)
			}
		},
		// id
		async deleteById({},id){
			await deleteCouponById(id)
		},
		async getCouponTypeListAll({commit}){
			let res = await getCouponTypeList()
			if(res.data.code == 200){
				commit('setCouponTypeList',res.data.data.list)
			}
		},
		async insert({},addForm){
			await insertCoupon(addForm)
		},
		async findById({},id){
			let res = await findCouponById(id)
			if(res.data.code == 200){
				return res.data.data
			}else{
				return {}
			}
		},
		async update({},addForm){
			await updateCoupon(addForm)
		},
		async setIsOnSale({},params){
			await setCouponIsOnSale(params)
		},
		async insertMultiple({},addForm){
			await insertCouponMultiple(addForm)
		}
	}
}