//store/index.js
// user-api
import { getLogListForPage} from '../../../api/log-api.js'
// 
export default{
	// mapState
	namespaced:true,
	// 
	// statevuedata
	//statemapStatevuecomputedstatevue
	state:{
		list:[],//table
		page:{//
			pno:1,
			psize:10,
			pCount:0,
			totalElements:0
		}
	},
	// mutationsstate
	// mapState
	// mutationsstate
	//mutationmapMutationsvuemethods
	mutations:{
		// statelist
		setList(state,list){
			state.list = list;
		},
		// statepage
		setPage(state,page){
			state.page = page
		}
	},
	// actions
	//
	//actionspromiseawait
	//actionscommitmutationsset
	//dispatchactions
	//actionsmapActionsvuemethods
	actions:{
		async getListForPage({commit},data){
			// 
			let res = await getLogListForPage(data);
			if(res.data.code == 200){
				// code200listpage
				//mutationslistpage
				commit('setList',res.data.data.list)
				commit('setPage',res.data.data.page)
			}
		},
		
		
		
	}
}