import api from '../http'
// loginapihttp
export const login = (username,password) => {
	return api.http({
		url:'/user/admin/login',
		method:'post',
		data:{
			username,
			password
		}
	})
}
// 
export const getUserList = (data) => {
	return api.http({
		url:'/user/admin/list/page',
		params:{
			pno:data.pno,
			psize:data.psize,
			username:data.username
		},
		method:'get'
	})
}
// 
export const getUserListAll = (data) => {
	return api.http({
		url:'/user/admin/list/all',
		params:{
			pno:data.pno,
			psize:data.psize,
			username:data.username
		},
		method:'get'
	})
}
//id
export const removeUserById = (id) => {
	return api.http({
		url:`/user/admin/delete/id/${id}`,
		method:'delete'
	})
}
//
export const addUser = (user) => {
	return api.http({
		url:`/user/admin/insert`,
		method:'put',
		data:user
	})
}
//id
export const findUserById = (id) => {
	return api.http({
		url:`/user/admin/find/id/${id}`,
		method:'get',
	})
}

export const updateUser = (user) => {
	return api.http({
		url:`/user/admin/update`,
		method:'put',
		data:user
	})
}

export const changePass = (user) => {
	return api.http({
		url:`/user/admin/password/change`,
		method:'post',
		data:user
	})
}