//store/index.js
// user-api
import { getUserList,removeUserById,addUser,findUserById,updateUser,getUserListAll} from '../../../api/user-api.js'
// role-api
import { getAllRoleList } from '../../../api/role-api.js'
// 
export default{
	// mapState
	namespaced:true,
	// 
	// statevuedata
	//statemapStatevuecomputedstatevue
	state:{
		list:[],//table
		page:{//
			pno:1,
			psize:10,
			pCount:0,
			totalElements:0
		},
		roleList:[]//
	},
	// mutationsstate
	// mapState
	// mutationsstate
	//mutationmapMutationsvuemethods
	mutations:{
		// statelist
		setList(state,list){
			state.list = list;
		},
		// statepage
		setPage(state,page){
			state.page = page
		},
		// state
		setRoleList(state,roleList){
			state.roleList = roleList
		}
	},
	// gettersvuexcomputedstategetter
	getters:{
		getList(state,getter){
			return state.list
		}
	},
	// actions
	//
	//actionspromiseawait
	//actionscommitmutationsset
	//dispatchactions
	//actionsmapActionsvuemethods
	actions:{
		async getListAll({commit},data){
			let res = await getUserListAll(data);
			if(res.data.code == 200){
				// code200listpage
				//mutationslistpage
				let list = res.data.data.list.map((item,index) => {
					item.index = index+1
					return item
				})
					
				let page = {
					pno:1,
					psize:10,
					totalElements:list.length
				}
				commit('setList',list)
				commit('setPage',page)
			}
		},
		// 
		//actions{commit,dispatch}mutationsactions
		//vuejson
		async getListForPage({commit},data){
			// 
			let res = await getUserList(data);
			if(res.data.code == 200){
				// code200listpage
				//mutationslistpage
				commit('setList',res.data.data.list)
				commit('setPage',res.data.data.page)
			}
		},
		// id
		async deleteById({dispatch},id){
			await removeUserById(id);
		},
		async getRoleListAll({commit}){
			let res = await getAllRoleList();
			if(res.data.code == 200){
				commit('setRoleList',res.data.data.list)
			}
		},
		async insert({},user){
			await addUser(user)
		},
		async findById({},id){
			let res = await findUserById(id)
			if(res.data.code == 200 ){
				let data = res.data.data
				delete data.password
				return data
			}
		},
		async update({},user){
			await updateUser(user)
		},
		
		
	}
}