//store/index.js
// user-api
import { getTeamListForPage,insertTeam 
	,updateTeam,findTeamById,deleteTeamById,setTeamIsOnSale,getTeamUserList} from '../../../api/team-api.js'
// 
import {getGoodsListAll} from '../../../api/goods-api.js'
export default{
	// mapState
	namespaced:true,
	// 
	// statevuedata
	//statemapStatevuecomputedstatevue
	state:{
		list:[],//table
		page:{//
			pno:1,
			psize:10,
			pCount:0,
			totalElements:0
		},
		teamUserList:[],
		goodsList:[]
	},
	// mutationsstate
	// mapState
	// mutationsstate
	//mutationmapMutationsvuemethods
	mutations:{
		// statelist
		setList(state,list){
			state.list = list;
		},
		// statepage
		setPage(state,page){
			state.page = page
		},
		setGoodsList(state,goodsList){
			state.goodsList = goodsList
		},
		setTeamUserList(state,teamUserList){
			state.teamUserList = teamUserList
		}
	},
	
	actions:{
		async getGoodsListAll({commit}){
			let res = await getGoodsListAll()
			if(res.data.code == 200){
				commit("setGoodsList",res.data.data.list)
			}
		},
		async getTeamUserList({commit},id){
			let res = await getTeamUserList(id)
			if(res.data.code == 200){
				commit("setTeamUserList",res.data.data.list)
			}
		},
		// 
		//actions{commit,dispatch}mutationsactions
		//vuejson
		async getListForPage({commit},queryForm){
			// 
			let res = await getTeamListForPage(queryForm);
			if(res.data.code == 200){
				// code200listpage
				//mutationslistpage
				commit('setList',res.data.data.list)
				commit('setPage',res.data.data.page)
			}
		},
		// id
		async deleteById({},id){
			await deleteTeamById(id)
		},
		async insert({},addForm){
			await insertTeam(addForm)
		},
		async findById({},id){
			let res = await findTeamById(id)
			if(res.data.code == 200){
				return res.data.data
			}else{
				return {}
			}
		},
		async update({},addForm){
			await updateTeam(addForm)
		},
		async setIsOnSale({},params){
			await setTeamIsOnSale(params)
		}
		
	}
}