import Vue from 'vue'
import Vuex from 'vuex'
// useruser-model.js
import UserModel from '../views/system/user/user-model.js'
import RoleModel from '../views/system/role/role-model.js'
import MenuModel from '../views/system/menu/menu-model.js'
import GoodsModel from '../views/shop/goods/goods-model.js'
import NewsModel from '../views/shop/news/news-model.js'
import UserTestModel from '../views/test/user-test/user-test-model.js'
import ShopUserModel from '../views/shop/shop-user/shop-user-model.js'
import CardModel from '../views/shop/card/card-model.js'
import CouponModel from '../views/shop/coupon/coupon-model.js'
import OrderModel from '../views/shop/order/order-model.js'
import CardTypeModel from '../views/type/card-type/card-type-model.js'
import CouponTypeModel from '../views/type/coupon-type/coupon-type-model.js'
import GoodsTypeModel from '../views/type/goods-type/goods-type-model.js'
import UserTypeModel from '../views/type/user-type/user-type-model.js'
import NewsTypeModel from '../views/type/news-type/news-type-model.js'
import DeptModel from '../views/dept/dept/dept-model.js'
import UserStatusModel from '../views/type/user-status/user-status-model.js'
import LogModel from '../views/system/log/log-model.js'
import TeamModel from '../views/shop/team/team-model.js'
Vue.use(Vuex)
// id
import { getMenuByRoleId } from '../api/menu-api.js'
//
const store = new Vuex.Store({
	// vuexVuedataVue.observable
  state: {
    menuList: [],//
		tabsList:[],//tabs
		activeMenuId:'',//id
		selectedMenu:[]
  },
	// mutationsstateVuex
	//statemutationsstate
  mutations: {
		// 
    setMenuList (state,menuList) {
      state.menuList = menuList;
			sessionStorage.menuList = JSON.stringify(menuList);
    },
		// tabs
		setTabsList (state,tabsList) {
			state.tabsList = tabsList
			sessionStorage.tabsList = JSON.stringify(tabsList);
		},
		// 
		setActiveMenuId (state, activeMenuId){
			state.activeMenuId = activeMenuId
			sessionStorage.activeMenuId = activeMenuId;
		}
  },
	// gettersVuexVuecomputed
	getters:{
		getActiveMenuId(state){
			state.activeMenuId = sessionStorage.activeMenuId?sessionStorage.activeMenuId:state.sessionStorage
			return state.activeMenuId;
		},
		getTabsList(state){
			try{
				let tabsList = JSON.parse(sessionStorage.tabsList)
				state.tabsList = tabsList
			}catch(e){
				
			}
			return state.tabsList;
		},
		// 
		getMenuList(state,getters){
			try{
				let menuList = JSON.parse(sessionStorage.menuList)
				state.menuList = menuList
			}catch(e){
				
			}
			return state.menuList;
		},
		// tabsListkeep-alivename
		getKeepAliveList(state){
			return state.tabsList.map(item => item.url.replace('/',''));
		},
		// 
		getActiveMenu(state,getters){
			
			let menuList = getters.getMenuList;
			let selectedMenu = {}
			menuList.forEach(item => {
				if(item.children){
					item.children.forEach(itemChild => {
						if(itemChild.id == getters.getActiveMenuId){
							selectedMenu = itemChild;
						}
					})
				}
			})
			state.selectedMenu = selectedMenu
			return state.selectedMenu;
		}
	},
	// actionsVuex
	actions:{
		// idmenuList,tabsList,activeMenuId
		async fetchMenuByRoleId({commit},data){
			let res = await getMenuByRoleId(data)
			if(res.data.code == 200){
				let menuList = res.data.data;
				// commitmutations
				commit('setActiveMenuId',menuList[0].children[0].id)
				commit('setTabsList',[menuList[0].children[0]])
				commit('setMenuList',menuList)
			}
		}
	},
	modules:{
		// user-model.jsstoreuserModel
		userModel:UserModel,
		roleModel:RoleModel,
		menuModel:MenuModel,
		userTestModel:UserTestModel,
		goodsModel:GoodsModel,
		shopUserModel:ShopUserModel,
		newsModel:NewsModel,
		cardModel:CardModel,
		couponModel:CouponModel,
		orderModel:OrderModel,
		cardTypeModel:CardTypeModel,
		couponTypeModel:CouponTypeModel,
		goodsTypeModel:GoodsTypeModel,
		userTypeModel:UserTypeModel,
		newsTypeModel:NewsTypeModel,
		deptModel:DeptModel,
		userStatusModel:UserStatusModel,
		logModel:LogModel,
		teamModel:TeamModel
	}
})
export default store