//store/index.js
// user-api
import { getOrderListForPage,findOrderById,deleteOrderById,sendGoods,fallbackGoodsOver} from '../../../api/order-api.js'
// 
export default{
	// mapState
	namespaced:true,
	// 
	// statevuedata
	//statemapStatevuecomputedstatevue
	state:{
		list:[],//table
		page:{//
			pno:1,
			psize:10,
			pCount:0,
			totalElements:0
		},
	},
	// mutationsstate
	// mapState
	// mutationsstate
	//mutationmapMutationsvuemethods
	mutations:{
		// statelist
		setList(state,list){
			state.list = list;
		},
		// statepage
		setPage(state,page){
			state.page = page
		},
	},
	// gettersvuexcomputedstategetter
	getters:{
		getList(state,getter){
			return state.list
		}
	},
	
	actions:{
		// 
		//actions{commit,dispatch}mutationsactions
		//vuejson
		async getListForPage({commit},queryForm){
			// 
			let res = await getOrderListForPage(queryForm);
			if(res.data.code == 200){
				// code200listpage
				//mutationslistpage
				commit('setList',res.data.data.list)
				commit('setPage',res.data.data.page)
			}
		},
		// id
		async deleteById({},id){
			await deleteOrderById(id)
		},
		async findById({},id){
			let res = await findOrderById(id)
			if(res.data.code == 200){
				return res.data.data
			}else{
				return {}
			}
		},
		async send({},params){
			await sendGoods(params)
		},
		async fallbackGoodsOver({},id){
			await fallbackGoodsOver(id)
		}
	}
}