import { 
	getDeptListForPage,
	deleteDeptById,
	insertDept,
	findDeptById,
	updateDept,
	getDeptListUser,
	addDeptUser,
	deleteDeptUserById
} from '../../../api/dept-api.js'

export default{
	// mapState
	namespaced:true,
	state:{
		list:[],//table
		listUser:[],
		page:{//
			pno:1,
			psize:10,
			pCount:0,
			totalElements:0
		},
		userTypeList:[]
	},
	mutations:{
		// statelist
		setList(state,list){
			state.list = list;
		},
		// statepage
		setPage(state,page){
			state.page = page
		},
		setListUser(state,listUser){
			state.listUser = listUser
		}
	},
	// gettersvuexcomputedstategetter
	getters:{
		getList(state,getter){
			return state.list
		}
	},
	actions:{
		async getListForPage({commit},data){
			// 
			let res = await getDeptListForPage(data);
			if(res.data.code == 200){
				commit('setList',res.data.data.list)
				commit('setPage',res.data.data.page)
			}
		},
		async getListForUser({commit},data){
			// 
			let res = await getDeptListUser(data);
			if(res.data.code == 200){
				commit('setListUser',res.data.data)
			}
		},
		// id
		async deleteById({dispatch},id){
			await deleteDeptById(id);
		},
		async getUserTypeListAll({commit}){
			let res = await getUserTypeList();
			if(res.data.code == 200){
				commit('setUserTypeList',res.data.data.list)
			}
		},
		async insert({},addForm){
			await insertDept(addForm)
		},
		async update({},addForm){
			await updateDept(addForm)
		},
		async findById({},id){
			let res = await findDeptById(id)
			if(res.data.code == 200 ){
				return res.data.data
			}
		},
		async addDeptUser({commit},params){
			await addDeptUser(params)
		},
		async deleteUserById({commit},id){
			await deleteDeptUserById(id)
		}
	},
	
}