<template>
	<!-- 
		el-menutabs
		Vuex
	 -->
	<div class="p-menu">
		<el-menu
			:default-active="getActiveMenuId"
			router
			background-color="#333744"
			text-color="#fff"
			@select="handleSelect"
			class="el-menu-vertical-demo">
			<!-- 
				listlist
				indexid
			 -->
			<el-submenu v-for="menu in getMenuList" :key="menu.id" :index="''+menu.id">
				<!-- templatesubmenu -->
				<template slot="title">
					<i :class="menu.icon"></i>
					<span>{{menu.name}}</span>
				</template>
				<!-- 
					list
				  v-for
					index
					routepathel-menuroutertrue
				 -->
				<el-menu-item v-for="menuChild in menu.children " 
					:key="menuChild.id"
					:index="''+menuChild.id"  :route="menuChild.url">
					<i :class="menuChild.icon"></i>
					<span slot="title">{{menuChild.name}}</span>
				</el-menu-item>
			</el-submenu>
		</el-menu>
	</div>
</template>

<script>
	// Vuexmutationstate
	import { mapMutations,mapState,mapGetters } from 'vuex'
	export default{
		name:'p-menu',
		computed:{
			// vuexstatestate
			...mapState(['tabsList','activeMenuId']),
			...mapGetters(['getMenuList','getActiveMenuId'])
		},
		methods:{
			// vuexmutationsmethods
			// statemutations
			...mapMutations(['setActiveMenuId','setTabsList']),
			handleSelect(res){
				let selectedMenu = {}
				this.getMenuList.forEach(item => {
					if(item.children){
						item.children.forEach(itemChild => {
							if(itemChild.id == res){
								selectedMenu = itemChild;
							}
						})
					}
				})
				let arr = [...this.tabsList];
				let has = false
				arr.forEach(item => {
					if(item.id == res){
						has = true;
					}
				})
				if(has == false){
					arr.push(selectedMenu)
				}
				this.setActiveMenuId(res);
				this.setTabsList(arr)
			}
		}
		
	}
</script>

