//store/index.js
// user-api
import { getNewsListForPage,insertNews 
	,updateNews,findNewsById,deleteNewsById} from '../../../api/news-api.js'
import { getNewsTypeList } from '../../../api/news-type-api.js'
// 
export default{
	// mapState
	namespaced:true,
	// 
	// statevuedata
	//statemapStatevuecomputedstatevue
	state:{
		list:[],//table
		page:{//
			pno:1,
			psize:10,
			pCount:0,
			totalElements:0
		},
		newsTypeList:[]
	},
	// mutationsstate
	// mapState
	// mutationsstate
	//mutationmapMutationsvuemethods
	mutations:{
		// statelist
		setList(state,list){
			state.list = list;
		},
		// statepage
		setPage(state,page){
			state.page = page
		},
		// state
		setNewsTypeList(state,newsTypeList){
			state.newsTypeList = newsTypeList
		}
	},
	// gettersvuexcomputedstategetter
	getters:{
		getList(state,getter){
			return state.list
		}
	},
	
	actions:{
		// 
		//actions{commit,dispatch}mutationsactions
		//vuejson
		async getListForPage({commit},queryForm){
			// 
			let res = await getNewsListForPage(queryForm);
			if(res.data.code == 200){
				// code200listpage
				//mutationslistpage
				commit('setList',res.data.data.list)
				commit('setPage',res.data.data.page)
			}
		},
		// id
		async deleteById({},id){
			await deleteNewsById(id)
		},
		async getNewsTypeListAll({commit}){
			let res = await getNewsTypeList()
			if(res.data.code == 200){
				commit('setNewsTypeList',res.data.data.list)
			}
		},
		async insert({},addForm){
			await insertNews(addForm)
		},
		async findById({},id){
			let res = await findNewsById(id)
			if(res.data.code == 200){
				return res.data.data
			}else{
				return {}
			}
		},
		async update({},addForm){
			await updateNews(addForm)
		},
		async setIsOnSale({},params){
			await setNewsIsOnSale(params)
		}
		
	}
}