//store/index.js
// user-api
import { getCardListForPage,insertCard 
	,updateCard,findCardById,deleteCardById,insertCardMultiple,openCard} from '../../../api/card-api.js'
import { getCardTypeList } from '../../../api/card-type-api.js'
// 
export default{
	// mapState
	namespaced:true,
	// 
	// statevuedata
	//statemapStatevuecomputedstatevue
	state:{
		list:[],//table
		page:{//
			pno:1,
			psize:10,
			pCount:0,
			totalElements:0
		},
		cardTypeList:[]
	},
	// mutationsstate
	// mapState
	// mutationsstate
	//mutationmapMutationsvuemethods
	mutations:{
		// statelist
		setList(state,list){
			state.list = list;
		},
		// statepage
		setPage(state,page){
			state.page = page
		},
		// state
		setCardTypeList(state,cardTypeList){
			state.cardTypeList = cardTypeList
		}
	},
	// gettersvuexcomputedstategetter
	getters:{
		getList(state,getter){
			return state.list
		}
	},
	
	actions:{
		// 
		//actions{commit,dispatch}mutationsactions
		//vuejson
		async getListForPage({commit},queryForm){
			// 
			let res = await getCardListForPage(queryForm);
			if(res.data.code == 200){
				// code200listpage
				//mutationslistpage
				commit('setList',res.data.data.list)
				commit('setPage',res.data.data.page)
			}
		},
		// id
		async deleteById({},id){
			await deleteCardById(id)
		},
		async getCardTypeListAll({commit}){
			let res = await getCardTypeList()
			if(res.data.code == 200){
				commit('setCardTypeList',res.data.data.list)
			}
		},
		async insert({},addForm){
			await insertCard(addForm)
		},
		async findById({},id){
			let res = await findCardById(id)
			if(res.data.code == 200){
				return res.data.data
			}else{
				return {}
			}
		},
		async update({},addForm){
			await updateCard(addForm)
		},
		async insertMultiple({},addForm){
			await insertCardMultiple(addForm)
		},
		async open({},params){
			await openCard(params)
		}
	}
}