import { 
	getUserStatusListForPage,
	deleteUserStatusById,
	insertUserStatus,
	findUserStatusById,
	updateUserStatus
} from '../../../api/user-status-api.js'

export default{
	// mapState
	namespaced:true,
	state:{
		list:[],//table
		page:{//
			pno:1,
			psize:10,
			pCount:0,
			totalElements:0
		},
		UserStatusList:[]
	},
	mutations:{
		// statelist
		setList(state,list){
			state.list = list;
		},
		// statepage
		setPage(state,page){
			state.page = page
		}
	},
	// gettersvuexcomputedstategetter
	getters:{
		getList(state,getter){
			return state.list
		}
	},
	actions:{
		async getListForPage({commit},data){
			// 
			let res = await getUserStatusListForPage(data);
			if(res.data.code == 200){
				commit('setList',res.data.data.list)
				commit('setPage',res.data.data.page)
			}
		},
		// id
		async deleteById({dispatch},id){
			await deleteUserStatusById(id);
		},
		async getUserStatusListAll({commit}){
			let res = await getUserStatusList();
			if(res.data.code == 200){
				commit('setUserStatusList',res.data.data.list)
			}
		},
		async insert({},addForm){
			await insertUserStatus(addForm)
		},
		async findById({},id){
			let res = await findUserStatusById(id)
			if(res.data.code == 200 ){
				return res.data.data
			}
		},
		async update({},addForm){
			await updateUserStatus(addForm)
		}
	},
	
}