//store/index.js
// user-api
import { getGoodsListForPage,insertGoods 
	,updateGoods,findGoodsById,deleteGoodsById,setGoodsIsOnSale} from '../../../api/goods-api.js'
import { getGoodsTypeList } from '../../../api/goods-type-api.js'
// 
export default{
	// mapState
	namespaced:true,
	// 
	// statevuedata
	//statemapStatevuecomputedstatevue
	state:{
		list:[],//table
		page:{//
			pno:1,
			psize:10,
			pCount:0,
			totalElements:0
		},
		goodsTypeList:[]
	},
	// mutationsstate
	// mapState
	// mutationsstate
	//mutationmapMutationsvuemethods
	mutations:{
		// statelist
		setList(state,list){
			state.list = list;
		},
		// statepage
		setPage(state,page){
			state.page = page
		},
		// state
		setGoodsTypeList(state,goodsTypeList){
			state.goodsTypeList = goodsTypeList
		}
	},
	// gettersvuexcomputedstategetter
	getters:{
		getList(state,getter){
			return state.list
		}
	},
	
	actions:{
		// 
		//actions{commit,dispatch}mutationsactions
		//vuejson
		async getListForPage({commit},queryForm){
			// 
			let res = await getGoodsListForPage(queryForm);
			if(res.data.code == 200){
				// code200listpage
				//mutationslistpage
				commit('setList',res.data.data.list)
				commit('setPage',res.data.data.page)
			}
		},
		// id
		async deleteById({},id){
			await deleteGoodsById(id)
		},
		async getGoodsTypeListAll({commit}){
			let res = await getGoodsTypeList()
			if(res.data.code == 200){
				commit('setGoodsTypeList',res.data.data.list)
			}
		},
		async insert({},addForm){
			await insertGoods(addForm)
		},
		async findById({},id){
			let res = await findGoodsById(id)
			if(res.data.code == 200){
				return res.data.data
			}else{
				return {}
			}
		},
		async update({},addForm){
			await updateGoods(addForm)
		},
		async setIsOnSale({},params){
			await setGoodsIsOnSale(params)
		}
		
	}
}